import { Component, OnInit, Input, AfterViewInit } from '@angular/core';
import { DndDropEvent } from "ngx-drag-drop";
import 'chart.piecelabel.js';

@Component({
  selector: 'app-mono-plot',
  templateUrl: './mono-plot.component.html',
  styleUrls: ['./mono-plot.component.css']
})
export class MonoPlotComponent implements AfterViewInit {


  @Input() data:any[]
  @Input() labels:any[]
  @Input() datasets:any[]
  @Input() chartType:string = 'line'
  @Input() legend:boolean = true
  @Input() options : any 
  @Input() title : string
  
  charts = [
    //{id: 'line', name: "Line"},
    {id: 'pie', name: "Pie"},
    {id: 'doughnut', name: "Doughnut"},
    //{id: 'horizontalBar', name: "Horizontal Bar"},
    {id: 'bar', name: "Verical Bar"},
    //{id: 'radar', name: "Radar"},
  ];

 
  
  public xAxis:string[] = [
  ];

  public yAxis:string[] = [
  ];

  constructor() { }

 ngAfterViewInit() {

    this.options={
      responsive: true,
      title: {
        display: true,
        text: this.title,
        fontSize: 20
      },
      pieceLabel: {
        render: function (args) {
          const label = args.label,
                value = args.value;
          return label + ': ' + value;
        },
       arc: true,
       fontColor: '#000',
       position: 'outside',
       showZero:true,
       legend: {
        display: false,
        labels: {
          fontColor: 'rgb(255, 99, 132)'
        }
      }
    }
  };

    //this.labels=['Download Sales', 'In-Store Sales', 'Mail-Order Sales']
    //this.data=[350, 450, 100]

  }

  onDrop( event:DndDropEvent, list:any[] ) {

    let index = event.index;
    if( typeof index === "undefined" ) {
      index = list.length;
    }
    list.splice( index, 0, event.data );
    if (this.xAxis.length>0 && this.yAxis.length>0){
      alert("Both")
    }
  }

  onDragged( item:any, list:any[] ) {
    const index = list.indexOf( item );
    list.splice( index, 1 );
  }
 
  public chartClicked(e:any):void {
    console.log(e);
  }
 
  public chartHovered(e:any):void {
    console.log(e);
  }
}
